import React from 'react'

// images
import {} from "./../../../../assets/images/breadcum/investment.png"
//css file
import "./Hospitality_breadcum.css"
import { Link } from 'react-router-dom'

const Investment_breadcum = () => {
    return (
        <div>
            <div className='Invest_breadcrumb'>
                <div className='gradint_effect'></div>
                <div className='text-white career_bread_text'>
                    <h1 className='career_bread_head'>Raichand Hospitality</h1>
                    <div className='flex justify-center items-center gap-2 mt-1'>
                        <Link to="/" className='career_breadcum_link'><span>Home</span></Link>
                        <span>&#187;</span>
                        <Link to="/hospitality" className='text-white'><span>Raichand Hospitality</span></Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Investment_breadcum